import { Router } from '@angular/router';
import { Movie, MovieService } from './movie.service';
import { Component, OnInit } from '@angular/core';


@Component({
  selector: 'movie',
  template: `
  	<md-grid-list cols="3" rowHeight="3:4" >
	  <md-grid-tile *ngFor="let movie of movies" (click)="onSelect(movie)" style="cursor: pointer;">
	  	<md-card>
		  	<img [src]="movie.poster_link" md-card-image layout-fill/>
			</md-card>
			<md-grid-tile-footer>		
				<h3 md-line>{{movie.cht_title}}</h3>
				<span md-line>&nbsp;</span>
            <span md-line>IMDB: {{movie.imdb.rating}}</span>
				<span md-line>爛番茄: {{movie.tomatoes.meter}}</span>
				<md-icon>star_border</md-icon>
			</md-grid-tile-footer>
	  </md-grid-tile>
	  <md-grid-tile>
		<script async src="//pagead2.googlesyndication.com/pagead/js/adsbygoogle.js"></script>
			<!-- D12 -->
			<ins class="adsbygoogle"
				style="display:block"
				data-ad-client="ca-pub-1789797875470932"
				data-ad-slot="4271573656"
				data-ad-format="auto"></ins>
			<script>
			(adsbygoogle = window.adsbygoogle || []).push({});
		</script>
	  </md-grid-tile>
	</md-grid-list>
  `,
})
export class MovieComponent implements OnInit {
	private ms:MovieService
	private movies:Movie[]
	
	constructor(private _router: Router,private _ms:MovieService) {

	}

	ngOnInit() {
		//this.movies = this.ms.getMovies()
		this._ms.getMovies().subscribe(movies=>{
			this.movies = movies;
		})
  	}

	onSelect(movie:Movie) {
		console.log(movie);
		this._router.navigate(['/movie', movie.id])
	}
}


